blocJams.service('SongPlayer', ['$rootScope',"Metric", function($rootScope,Metric) {
    var currentSoundFile = null;
    var trackIndex = function(currentAlbum, song) {
        return album.songs.indexOf(song);
    };

    return {
        currentSong: null,
        currentAlbum: null,
        playing: false,
        volume: 90,

        play: function() {
            this.playing = true;
            currentSoundFile.play();
            Metric.registerSongPlay(this.currentSong);
            console.log(this.currentAlbum);
            Metric.registerAlbumClicks(this.currentAlbum);
            Metric.registerArtistsClicksClicks(this.currentAlbum);
            //Metric.registerArtistsClicks(this.)
        },
        pause: function() {
            this.playing = false;
            currentSoundFile.pause();
        },
        next: function() {
            var currentTrackIndex = trackIndex(this.currentAlbum, this.currentSong);
            currentTrackIndex++;
            if (currentTrackIndex >= this.currentAlbum.songs.length) {
                    currentTrackIndex = 0;
            }
                var song = this.currentAlbum.songs[currentTrackIndex];
                this.setSong(this.currentAlbum, song);
        },
        previous: function() {
            var currentTrackIndex = trackIndex(this.currentAlbum, this.currentSong);
            currentTrackIndex--;
            if (currentTrackIndex < 0) {
                currentTrackIndex = this.currentAlbum.songs.length - 1;
            }
                var song = this.currentAlbum.songs[currentTrackIndex];
                this.setSong(this.currentAlbum, song);
        },
        seek: function(time) {
            // Checks to make sure that a sound file is playing before seeking.
            if (currentSoundFile) {
                // Uses a Buzz method to set the time of the song.
                currentSoundFile.setTime(time);
            }
        },
            onTimeUpdate: function(callback) {
                return $rootScope.$on('sound:timeupdate', callback);
            },

        setVolume: function(volume) {
            if(currentSoundFile){
                currentSoundFile.setVolume(volume);
            }
            this.volume = volume;
        },

        setSong: function(album, song) {
            if (currentSoundFile) {
                currentSoundFile.stop();
            }
            this.currentAlbum = album;
            this.currentSong = song;

            currentSoundFile = new buzz.sound(song.audioUrl, {
                formats: [ "mp3" ],
                preload: true
            });
                currentSoundFile.bind('timeupdate', function(e){
                    $rootScope.$broadcast('sound:timeupdate', this.getTime());
                });

            this.play();
        }
    };
}]);

blocJams.directive('slider', ['$document', function($document){
    var calculateSliderPercentFromMouseEvent = function($slider, event) {
        var offsetX =  event.pageX - $slider.offset().left;
        var sliderWidth = $slider.width();
        var offsetXPercent = (offsetX  / sliderWidth);
        offsetXPercent = Math.max(0, offsetXPercent);
        offsetXPercent = Math.min(1, offsetXPercent);
        return offsetXPercent;
    };

    var numberFromValue = function(value, defaultValue) {
        if (typeof value === 'number') {
            return value;
        }

        if(typeof value === 'undefined') {
            return defaultValue;
        }

        if(typeof value === 'string') {
            return Number(value);
        }
    };

    return {
        templateUrl: '/templates/directives/slider.html',
        replace: true,
        restrict: 'E',
        scope: {
            onChange: '&'
        },
        link: function(scope, element, attributes) {
            scope.value = 0;
            scope.max = 100;
            var $seekBar = $(element);
                attributes.$observe('value', function(newValue) {
                    scope.value = numberFromValue(newValue, 0);
                });
                attributes.$observe('max', function(newValue) {
                    scope.max = numberFromValue(newValue, 100) || 100;
                });

            var percentString = function () {
                var value = scope.value || 0;
                var max = scope.max || 100;
                percent = value / max * 100;
                return percent + "%";
            };

            scope.fillStyle = function() {
                return {width: percentString()};
            };

            scope.thumbStyle = function() {
                return {left: percentString()};
            };
            scope.onClickSlider = function(event) {
                var percent = calculateSliderPercentFromMouseEvent($seekBar, event);
                scope.value = percent * scope.max;
                notifyCallback(scope.value);
            };
            scope.trackThumb = function() {
                $document.bind('mousemove.thumb', function(event){
                    var percent = calculateSliderPercentFromMouseEvent($seekBar, event);
                    scope.$apply(function(){
                        scope.value = percent * scope.max;
                        notifyCallback(scope.value);
                    });
                });

                //cleanup
                $document.bind('mouseup.thumb', function(){
                    $document.unbind('mousemove.thumb');
                    $document.unbind('mouseup.thumb');
                });
            };
            var notifyCallback = function(newValue) {
                if(typeof scope.onChange === 'function') {
                    scope.onChange({value: newValue});
                }
            };
        }
    };
}]);

blocJams.filter('timecode', function(){
    return function(seconds) {
        seconds = Number.parseFloat(seconds);

        // Returned when no time is provided.
        if (Number.isNaN(seconds)) {
            return '-:--';
        }

        // make it a whole number
        var wholeSeconds = Math.floor(seconds);

        var minutes = Math.floor(wholeSeconds / 60);

        remainingSeconds = wholeSeconds % 60;

        var output = minutes + ':';

        // zero pad seconds, so 9 seconds should be :09
        if (remainingSeconds < 10) {
            output += '0';
        }

        output += remainingSeconds;

        return output;
    }
});


